@use 'base/mixins' as m;
@use 'base/variables' as v;

.no-proyectos {

    text-align: center;
    margin-top: 3rem;
    list-style: none;

    a {
        color: v.$indigo;
        font-weight: bold;
    }
}

.listado-proyectos {
    list-style: none;
    padding: 0;
    margin-top: 3rem;

    @include m.grid(1, 2rem);

    @include m.tablet {
        @include m.grid(2, 2rem);
    }

    @include m.desktop {
        @include m.grid(3, 2rem);
    }

    .proyecto {
        background-color: lighten(v.$indigo, 5%);
        min-height: 12rem;
        display: flex;
        align-items: center;
        padding: 2rem;
        line-height: 2rem;
        border-radius: .5rem;

        &:hover {
            background-color: darken(v.$indigo, 5%);
        }

        p {
            color: v.$blanco;
        }
    }
}